<template>
  <div
    v-infinite-scroll="loadMore"
    :infinite-scroll-disabled="listLoading"
    :infinite-scroll-distance="10"
  >
    <ul class="d-list">
      <li class="d-item" v-for="(item, index) in list" :key="index" @click="switchItem(item.id)">
        <div class="d-item__hd">
          <img
            class="d-item__logo"
            src="https://zb.oschina.net/file/get?path=static/project-reward/cover/youxikaifa_2.png"
          />
          <ul>
            <li class="title">{{ item.title }}</li>
            <li class="tags">
              <span class="tag">程序开发</span>
            </li>
          </ul>
        </div>
        <div class="d-item__bd">
          <div class="column">
            <span class="label">需求预算：</span>
            <span>￥{{ item.reward / 100 }}</span>
          </div>
          <div class="column">
            <span class="label">技能要求：</span>
            <span>{{ item.requires || '无任何要求' }}</span>
          </div>
        </div>
        <div class="d-item__ft">
          <div class="ui-flex">
            <img class="d-item__avatar" :src="item.userInfo.avatar" />
            <div class="d-item__name">{{ item.userInfo.mobile }}</div>
          </div>
          <div class="ui-flex">
            <van-icon class="arrow-right" name="friends" />
            <span>{{ item.enrollList.length }}人</span>
            <van-icon class="arrow-right" name="arrow" />
          </div>
        </div>
      </li>
    </ul>
    <div class="loading" v-if="listLoading">
      <van-loading class="van-loading" size="24px"></van-loading>
      <span>加载中...</span>
    </div>
    <div class="loading" v-if="!listLoading && !hasMore">-我是有底线的-</div>
  </div>
</template>

<script>
import ynowApi from '../../api/ynow';
export default {
  data() {
    return {
      list: [],
      listLoading: false,
      skuVisible: false,
      pageIndex: 1,
      pageSize: 10,
      total: 0,
      hasMore: true,
      isRefresh: false,
      queryParams: {
        projectType: '不限',
        appType: '不限'
      }
    };
  },
  mounted() {
    this.getDataList();
  },
  methods: {
    loadMore() {
      if (this.listLoading) return;
      if (!this.hasMore) return;
      this.pageIndex++;
      this.getDataList();
    },
    switchItem(itemId) {
      this.$router.push(`/project/${itemId}`);
    },
    async getDataList() {
      this.listLoading = true;
      const params = {
        pageSize: this.pageSize,
        pageIndex: this.pageIndex,
        appType: this.queryParams.appType,
        projectType: this.queryParams.projectType
      };

      Object.keys(params).forEach(key => {
        if (params[key] === '不限') {
          delete params[key];
        }
      });

      let res = await ynowApi.getDemandList(params);
      this.listLoading = false;

      if (!res) return;
      if (!res.success) return this.$toast(res.errMsg);

      if (this.isRefresh) {
        this.list = res.data.list;
      } else {
        this.list = this.list.concat(res.data.list);
      }
      this.total = res.data.pageInfo.total;
      this.hasMore = this.list.length < this.total;
      this.isRefresh = false;
    },
    onConfirmSku(obj) {
      this.isRefresh = true;
      this.pageIndex = 1;
      this.queryParams = obj;
      this.getDataList();
    }
  }
};
</script>

<style lang="scss" scoped>
.view {
  min-height: 100vh;
  background: #f4f7f9;
}

.loading {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  text-align: center;
  color: #666;
  font-size: 12px;
  padding-bottom: 30px;
  .van-loading {
    margin-right: 8px;
  }
}

.d-list {
  overflow: hidden;
  padding: 12px;
}

.d-item {
  overflow: hidden;
  padding: 12px 12px;
  background: #fff;
  border-bottom: solid 1px #f5f5f5;
  margin-bottom: 8px;
  .tags {
    margin-top: 4px;
  }
  .tag {
    display: inline-block;
    padding: 2px 6px;
    font-size: 12px;
    color: #409eff;
    margin-right: 5px;
    background: rgba(64, 158, 255, 0.1);
  }
  &__logo {
    width: 55px;
    height: 55px;
    margin-right: 10px;
    background: #2a98ff;
  }
  &__avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
  }
  &__hd {
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 20px;
    font-weight: bold;
  }
  &__bd {
    font-size: 13px;
    line-height: 24px;
    padding-top: 10px;
    padding-bottom: 10px;
    color: #48576a;
  }
  &__ft {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    color: #48576a;
    padding-top: 10px;
    border-top: 1px solid #e7eff5;
  }
  &__name {
    margin-left: 4px;
  }
}

.filter {
  z-index: 25;
  position: sticky;
  top: 0;
  background: #fff;
  .btn-sku {
    margin-left: 10px;
    font-size: 14px;
  }
}
</style>



