<template>
  <div class="page">
    <div class="header">
      <div class="search-bar">
        <div class="form" @click="$router.push('/search')">
          <van-icon class="icon-search" name="search" />
          <span class="input">搜索</span>
        </div>
      </div>
      <FilterMenu></FilterMenu>
    </div>
    <div class="page__body">
      <RecommendBanner></RecommendBanner>
      <ProjectList></ProjectList>
    </div>
  </div>
</template>

<script>
import ProjectList from '@/biz-components/project-list';
import FilterMenu from '@/biz-components/filter-menu';
import RecommendBanner from '@/biz-components/recommend-banner';

export default {
  components: {
    FilterMenu,
    ProjectList,
    RecommendBanner
  }
};
</script>

<style lang="scss" scoped>
.page {
  padding-top: 74px;
}

.search-bar {
  height: 44px;
  padding: 7px 15px;
  background: #fff;
  box-sizing: border-box;
  .form {
    box-sizing: border-box;
    position: relative;
    display: flex;
    align-items: center;
    height: 30px;
    line-height: 30px;
    width: 100%;
    z-index: 1;
    border-radius: 3px;
    background: #f4f4f4;
  }
  .icon-search {
    margin-left: 6px;
  }
  .input {
    padding-left: 8px;
    font-size: 14px;
    color: #888b8e;
  }
}

.header {
  z-index: 5;
  position: fixed;
  width: 100%;
  top: 0;
}
</style>



