<template>
  <van-tabs class="list" @click="handleClick">
    <van-tab
      v-for="(item, index) in menuList"
      :key="index"
      :title="item.name"
      v-model="activeName"
      @click="handleClick(item)"
    />
  </van-tabs>
</template>

<script>
export default {
  data() {
    return {
      activeName: 'H5应用',
      menuList: [
        {
          name: 'H5应用',
          id: 1
        },
        {
          name: '微信应用',
          id: 2
        },
        {
          name: '小程序',
          id: 3
        },
        {
          name: '移动APP',
          id: 4
        },
        {
          name: '前端APP',
          id: 5
        },
        {
          name: 'NodeJS',
          id: 6
        },
        {
          name: 'Java',
          id: 7
        },
        {
          name: '数据库',
          id: 8
        }
      ]
    };
  },
  methods: {
    handleClick(cid) {
      this.$router.replace(`/?cid=${cid}`);
    }
  }
};
</script>

<style lang="scss" scoped>
.list {
  overflow: hidden;
  width: 100%;
}
</style>



